export function setEntryToCookies(id, obj) {
    //serialize the object
    const payload = JSON.stringify(obj);
    if (id === "") {
        return false;
    }
    document.cookie = `${id}=${payload};path=/;`;
    return true;
}
export function getEntriesFromCookies() {
    const cookies = document.cookie.split(";");
    let output = new Map();
    for (let cookie of cookies) {
        let [id, val] = cookie.split("=");
        if (!id || !val || id === "" || val === "") {
            continue;
        }
        let obj;
        try {
            obj = JSON.parse(val);
        }
        catch (e) {
            console.error("error: unexpected cookie value format");
            continue;
        }
        output.set(id, obj);
    }
    return output;
}
export function deleteEntryFromCookie(id) {
    document.cookie = `${id}=;path=/;max-age=0;`;
    return true;
}
export function makeID(obj) {
    return `${obj.NID}-${Date.now()}`;
}
